import React, { Fragment } from 'react';
import { Table } from 'react-bootstrap';
import './CommonTable.css';

const CommonTable = ({ children }) => (
  <Table className="data-table" responsive hover>
    {children}
  </Table>
);

const TdHead = ({ d, type }) =>
  type === 'thead' ? (
    <Fragment>
      <td className="td-head td-year"></td>
      <td className="td-head"></td>
      <td className="td-head"></td>
    </Fragment>
  ) : (
    <Fragment>
      <td className="td-head td-year">{d['']}</td>
      <td className="td-head">{d['']}</td>
      <td className="td-head">{d['']}</td>
    </Fragment>
  );
  
export default CommonTable;
export { TdHead };


