import React, { Fragment } from 'react';
import CommonTable, { TdHead } from '../CommonTable';
import PaneTemplate from './PaneTemplate';


const ExpTable = ({ data }) => (
  <CommonTable>
    <thead>
      <tr>
        <TdHead type="thead" />
        <td></td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
      </tr>
    </thead>
    <tbody>
      {data.map((d, i) => (
        <tr key={i}>
          <TdHead d={d} />
          {calcExpenseTds(d)}
        </tr>
      ))}
    </tbody>
  </CommonTable>
);

const ExpPane = ({ eventKey, data }) => (
  <PaneTemplate eventKey={eventKey}>
    <h3></h3>
    <div style={{textAlign: 'right'}}>
      <small>: </small>
    </div>
    <ExpTable data={data} />
  </PaneTemplate>
);

function calcExpenseTds(d) {
  return d[''] < 2011 ? (
    <Fragment>
      <td className="em">{d['']}</td>
      <td>{d['']}</td>
      <td colSpan={6}>{d['']}</td>
      <td colSpan={1}>{d['']}</td>
    </Fragment>
  ) : d[''] < 2016 ? (
    <Fragment>
      <td className="em">{d['']}</td>
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td colSpan={3}>{d['']}</td>
    </Fragment>
  ) : (
    <Fragment>
      <td className="em">{d['']}</td>
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td colSpan={2}>{d['']}</td>
    </Fragment>
  );
}

export default ExpPane;


