import React from 'react';
import CommonTable, { TdHead } from '../CommonTable';
import PaneTemplate from './PaneTemplate';
import Article from './Article';

import md from './PL.md';

const emphasized = true;

const PLProps = [
  { label: '' },
  { label: '' },
  { label: '', emphasized },
  { label: '' },
  { label: '' },
  { label: '', emphasized },
  { label: '' },
  { label: '' },
  { label: '', emphasized },
  { label: '' },
  { label: '', emphasized }
];

const PLTable = ({ data }) => (
  <CommonTable>
    <thead>
      <tr>
        <TdHead type="thead" />
        {PLProps.map((obj, i) => <td key={i}>{obj.label}</td>)}
      </tr>
    </thead>
    <tbody>
      {data.map((d, i) => (
        <tr key={i}>
          <TdHead d={d} />
          {PLProps.map(obj => (
            <td key={obj.label} className={obj.emphasized ? 'em' : ''}>
              {d[obj.label]}
            </td>
          ))}
        </tr>
      ))}
    </tbody>
  </CommonTable>
);

const PLPane = ({ eventKey, data }) => (
  <PaneTemplate eventKey={eventKey}>
    <h3></h3>
    <div style={{textAlign: 'right'}}>
      <small>: </small>
    </div>
    <PLTable data={data} />
    <Article md={md} />
  </PaneTemplate>
);

export default PLPane;



