import React from 'react';
import CommonTable, { TdHead } from '../CommonTable';
import PaneTemplate from './PaneTemplate';
import Article from './Article';
import { format as d3format } from 'd3-format';

import md from './BS.md';

const emphasized = true;

const BSProps = [
  { label: '', emphasized },
  { label: '' },
  { label: '' },
  { label: '', emphasized },
  { label: '' },
  { label: '' },
  { label: '', emphasized },
  { label: '' },
  { label: '' },
  { label: '' },
  { label: '' }
];

const BSTable = ({ data }) => (
  <CommonTable>
    <thead>
      <tr>
        <TdHead type="thead" />
        {BSProps.map((obj, i) => <td key={i}>{obj.label}</td>)}
        <td></td>
        <td></td>
      </tr>
    </thead>
    <tbody>
      {data.map((d, i) => (
        <tr key={i}>
          <TdHead d={d} />
          {BSProps.map(obj => (
            <td key={obj.label} className={obj.emphasized ? 'em' : ''}>
              {d[obj.label]}
            </td>
          ))}
          <td>
            {d[''] && d['']
              ? d3format('.0%')(d[''] / d[''])
              : ''}
          </td>
          <td>{d3format('.0%')(d[''] / d[''])}</td>
        </tr>
      ))}
    </tbody>
  </CommonTable>
);

const BSPane = ({ eventKey, data }) => (
  <PaneTemplate eventKey={eventKey}>
    <h3></h3>
    <div style={{textAlign: 'right'}}>
      <small>: </small>
    </div>
    <BSTable data={data} />
    <Article md={md} />
  </PaneTemplate>
);

export default BSPane;


