import React from 'react';
import CommonTable, { TdHead } from '../CommonTable';
import PaneTemplate from './PaneTemplate';

const AttdTable = ({ data }) => (
  <CommonTable>
    <thead>
      <tr>
        <TdHead type="thead" />
        <td></td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
      </tr>
    </thead>
    <tbody>
      {data.map((d, i) => (
        <tr key={i}>
          <TdHead d={d} />
          <td>{d['']}</td>
          <td>{Math.round(d[''])}</td>
          <td>{d['']}</td>
          <td>{d[''] || d['']}</td>
          <td>{Math.round(d[''])}</td>
        </tr>
      ))}
    </tbody>
  </CommonTable>
);

const AttdPane = ({ eventKey, data }) => (
  <PaneTemplate eventKey={eventKey}>
    <h3></h3>
    <div style={{textAlign: 'right'}}>
      <small>: </small>
    </div>
    <AttdTable data={data} />
  </PaneTemplate>
);

export default AttdPane;


