import React, { Fragment } from 'react';
import CommonTable, { TdHead } from '../CommonTable';
import PaneTemplate from './PaneTemplate';
import Article from './Article';

import md from './revenue.md';

const RevTable = ({ data }) => (
  <CommonTable>
    <thead>
      <tr>
        <TdHead type="thead" />
        <td></td>
        <td></td>
        <td></td>
        <td>J</td>
        <td></td>
        <td></td>
        <td></td>
      </tr>
    </thead>
    <tbody>
      {data.map((d, i) => (
        <tr key={i}>
          <TdHead d={d} />
          {calcRevenueTds(d)}
        </tr>
      ))}
    </tbody>
  </CommonTable>
);

const RevPane = ({ eventKey, data }) => (
  <PaneTemplate eventKey={eventKey}>
    <h3></h3>
    <div style={{textAlign: 'right'}}>
      <small>: </small>
    </div>
    <RevTable data={data} />
    <Article md={md} />
  </PaneTemplate>
);


function calcRevenueTds(d) {
  const BasicRevs = () => (
    <Fragment>
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td>{d['J']}</td>
    </Fragment>
  );

  return d[''] < 2011 ? (
    <Fragment>
      <td className="em">{d['']}</td>
      <BasicRevs />
      <td colSpan={3}>{d['']}</td>
    </Fragment>
  ) : d[''] < 2016 ? (
    <Fragment>
      <td className="em">{d['']}</td>
      <BasicRevs />
      <td>{d['']}</td>
      <td colSpan={2}>{d['']}</td>
    </Fragment>
  ) : (
    <Fragment>
      <td className="em">{d['']}</td>
      <BasicRevs />
      <td>{d['']}</td>
      <td>{d['']}</td>
      <td>{d['']}</td>
    </Fragment>
  );
}

export default RevPane;


